@mixin zIndex($zindex) {
	z-index: $zindex;
}

@mixin zIndexFarthest() {
	@include zIndex(-1); 
}

@mixin zIndexFar() {
	@include zIndex(0); 
}

@mixin zIndexClose() {
	@include zIndex(1); 
}

@mixin zIndexCloser() {
	@include zIndex(999); 
}

@mixin zIndexClosest() {
	@include zIndex(9999); 
}


.mdc-nav {
	&__hovermain {
		@include zIndexCloser();
	}
	&__topbar {
		@include zIndexClose();
	}
	&__hoverbar {
		@include zIndexClosest();
	}
}